import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { NavigationExtras, Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQueryCafe } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { SalesChartTableReport } from 'src/app/cafe/interface/vh-report';
import { LanguageService } from 'src/app/cafe/services/language.service';
import ApexCharts, { ApexOptions } from 'apexcharts';
import { DatePipe } from '@angular/common';
import { FunctionService } from 'src/app/cafe/services/function.service';
@Component({
  selector: 'app-sales-menu',
  templateUrl: './sales-menu.component.html',
  styleUrls: ['./sales-menu.component.scss']
})
export class SalesMenuComponent implements OnInit {
  public date: Date[] = [new Date(), new Date()];
  selectedValue: any = 'all';
  list_menu: any = [{ _id: 'all', name: this.languageService.translate('All') }]
  heightScroll: string;
  listData: any = [];

  reportConfig: any = {
    sales_btax : this.vhQueryCafe.getLocalAppSettingNameBranch('permission_branch').show_btax_report_sales_category
  }
  dataReport: SalesChartTableReport = this.initDataReport()
  chart: ApexCharts;

  isChart: boolean = false; // false là bảng true là chart
  sales_category: any = new Object({ _id: 'all' });
  dataColor: Array<any> = ["#Ffda45", "#48c0e3", "#f9be57", "#ee3d85", "#f8756d", "#c667ca", "#d6df3a", "#06467b", "#3c86c7", "#1fd2cd", "#373737"];
  useBranch: boolean = this.fncService.checkUsingBranch()
  branchList: Array<any> = []
  id_branch_report: string = ''
  constructor(
    private router: Router,
    public vhAlgorithm: VhAlgorithm,
    public languageService: LanguageService,
    private vhQueryCafe: VhQueryCafe,
    private vhComponent: VhComponent,
    private datePipe: DatePipe,
    private cdRef: ChangeDetectorRef,
    private fncService: FunctionService,
    private vhAuth: VhAuth
  ) {
    this.date[0].setHours(0, 0, 0, 0)
    this.date[1].setHours(23, 59, 59, 59)
    this.getBranches()
    let data: any = this.router.getCurrentNavigation().extras.state

    if (data) {
      let stateSubObj = data.stateObj;
      this.dataReport = stateSubObj.dataReport;
      this.date = [this.dataReport.startTime, this.dataReport.endTime];
      this.selectedValue = stateSubObj.selectedValue;
      this.id_branch_report = stateSubObj.id_branch_report
    }
  }

  ngOnInit(): void {
    this.getListMenu()
  }

  /**
   * get danh sách menu để chọn xem báo cáo 
   * @example this.getListMenu()
   */
  getListMenu() {
    for (let item of this.vhQueryCafe.getlocalCategorys(1234)) {
      this.list_menu.push({ _id: item._id, name: item.name })
    }
  }

  /**
   * get chi nhánh được xem báo cáo của user
   */
  getBranches() {
    let branches = [...this.vhQueryCafe.getlocalBranchs()] // danh sách toàn bộ chi nhánh
    let employee = this.vhAuth.getUser() // thông tn nhân viên
    let defaultBranch = this.vhQueryCafe.getDefaultBranch()._id // chi nhánh mặc định khi đăng nhập (lấy id chi nhánh)

    if (this.useBranch) { // kiểm tra xem có 2 chi nhánh trở lên

      if (employee['owner'] == 'boss') { // kiểm tra tài khoản đăng nhập là boss
        this.branchList = branches // gán danh sách chi nhánh show ra HTML
        if (this.branchList.length > 1) this.branchList.unshift({ _id: "all", name: "All" })
        this.id_branch_report = defaultBranch // gán mặc định chi nhánh mặc định xem báo cáo là chi nhánh đăng nhập vô
      }

      else { // trường hợp tài khoản là nhân viên
        this.vhAuth.getPermission(employee['_id']).then(result => { // get quyền nhân viên được xem báo cáo chi nhánh nào
          branches.forEach(item => {
            if (result[`${item._id}_view_report_branch`]) this.branchList.push({ ...item }) // kiểm tra chi nhánh đc xem báo cáo, gán cho branchList show ra HMLT 
          })
          if (this.branchList.length > 1) { // trường hợp được xem báo cáo 2 chi nhánh trở lên
            this.branchList.unshift({ _id: "all", name: "All" }) // đẩy xem tất cả vô, nếu có 2 chi nhánh trở lên
            for (let i in this.branchList) {
              if (this.branchList[i]._id == defaultBranch) return this.id_branch_report = defaultBranch // nếu có gán chi nhánh xem báo mặc định là chi nhánh mặc định (đăng nhập)
              else this.id_branch_report = this.branchList[1]._id // ngược lại gán chi nhánh mặc định là chi nhánh thứ 1
            }
          }
          else if (this.branchList.length == 1) { // nếu chỉ đc xem báo cáo 1 chi nhánh , lấy mặc định là chi nhánh đó
            this.id_branch_report = this.branchList[0]._id
          }
          else this.vhComponent.alertMessageDesktop("warning", "Bạn không được chọn chi nhánh xem báo cáo") // kg có chi nhánh xem báo cáo
        })
      }
    } else this.id_branch_report = defaultBranch // chỉ xài 1 chi nhánh 

  }
  /**
   * bắt sk thay đổi menu xem báo cáo
   * @param optionValue 
   */
  filterProducts(optionValue) {
    this.sales_category['_id'] = optionValue
    if (this.sales_category['_id'] != 'all') this.isChart = false
    this.dataReport = this.initDataReport(this.date[0], this.date[1]);
  }


  initDataReport(startTime: Date = new Date(), endTime: Date = new Date()): SalesChartTableReport {
    startTime.setHours(0, 0, 0, 0)
    endTime.setHours(23, 59, 59, 59)
    return { startTime, endTime, dataChart: [], dataTable: { docs: [], sales_atax: 0, sales_btax: 0, quantity: 0 } }
  }

  /**
   * khởi tạo chart và gán dữ liệu cho chart
   * @param series 
   * @param labels 
   * @param initStatus 
   * 
   */
  initOption(series, labels, initStatus = false): ApexOptions {
    return {
      series: series,
      chart: {
        height: "auto",
        type: "pie"
      },
      dataLabels: {
        dropShadow: {
          enabled: false
        }
      },
      colors: this.dataColor,
      labels: labels,
      fill: {
        type: "solid",
        colors: this.dataColor,
      },
      legend: {
        show: false
      },
      yaxis: {
        labels: {
          formatter: (value) => {
            return this.vhAlgorithm.vhcurrencyunit(value);
          }
        }
      },
      states: {
        active: {
          filter: {
            type: "none"
          }
        },
        normal: {
          filter: {
            type: "none"
          }
        },
        hover: {
          filter: {
            type: "none"
          }
        }
      },
      noData: {
        text: this.languageService.translate('No data!')
      }
    }
  }

  /**
   * bắt sk thay đổi thời gian xem báo cáo
   * @param event 
   * @example this.changeTime(['2023-06-27T01:21:41.397Z','2023-06-27T01:21:41.397Z'])
   */
  changeTime(event) {
    if (new Date(event[1].setHours(0,0,0,0)).getTime() - new Date(event[0].setHours(0,0,0,0)).getTime() <=  this.fncService.checkDateReport() * 86000000) {
      this.dataReport = this.initDataReport(event[0], event[1])
      if (this.isChart) this.chart.updateOptions(this.initOption([], []))
    } else {
      this.vhComponent.showToast(4000, this.languageService.translate("The time limit to view is") + " " + this.fncService.checkDateReport() + " " +  this.languageService.translate("days"), "alert-toast")
      this.date = [new Date(event[0]), new Date(event[0].getTime() + this.fncService.checkDateReport() * 86000000)]
      this.dataReport = this.initDataReport(new Date(this.date[0]), new Date(this.date[1]))
    }
  }

  /**
   * xem báo cáo
   * @param startTime 
   * @param endTime 
   * @returns 
   */
  getSalesMenu(startTime, endTime) {
    return new Promise<any>((resolve, rejects) => {
      this.vhQueryCafe.getReportSales_Categorys_byCategorys(startTime, endTime, this.sales_category['_id'], 123, this.id_branch_report)
        .then((res) => resolve(res)).catch(err => rejects(err))
    })
  }


  /**
   * get dữ liệu báo cáo
   * @param startTime 
   * @param endTime 
   * @returns 
   */
  getSales(id_category, startTime, endTime) {
    this.vhComponent.showLoading("", "transparent-loading").then(() => {
      this.dataReport = this.initDataReport(startTime, endTime)
      this.getSalesMenu(startTime, endTime).then((res) => {
        this.vhComponent.hideLoading(0)
        if (res) {
          if (!res.empty) {
            let data = res.docs;
            for (let i = 0; i < 10; i++) {
              if (!data[i]) break
              else {
                this.dataReport['dataChart'].push({
                  color: this.dataColor[i],
                  label: data[i]['name'],
                  value: data[i]['sales_atax'],
                })
              }
            }

            this.dataReport['dataTable']['sales_atax'] = res['sales_atax']
            this.dataReport['dataTable']['sales_btax'] = res['sales_btax']
            this.dataReport['dataTable']['quantity'] = res['quantity']
            this.dataReport['dataTable']['docs'] = res.docs.map((item, index) => { return { ...item, index } })
            this.sort(this.statusQuantity, 'quantity')
            // this.searchProduct(searchValue)
          }
          if (this.isChart) this.updateChart(this.dataReport['dataChart'], false)
        }
      }).catch(() => this.vhComponent.hideLoading(0))
    });

  }
  //** hàm sort cho các cột */
  statusQuantity = false
  statusSales = false
  statusbSales = false
  statusName = false
  /**
   * sort với trường là dạng số
   * @param sort 
   * @param value tên trường cần sort
   * @example this.sort(1, 'total')
   */
  sort(sort, value) {
    if (sort) {
      this.dataReport.dataTable.docs = this.vhAlgorithm.sortNumberbyASC([...this.dataReport.dataTable.docs], value)
    }
    else {
      this.dataReport.dataTable.docs = this.vhAlgorithm.sortNumberbyDESC([...this.dataReport.dataTable.docs], value)
    }
  }
  /**
   * sort với trường là dạng chuỗi
   * @param sort 
   * @param value tên trường cần sort
   * @example this.sort(1, 'name')
   */
  sortName(sort, value) {
    if (sort) {
      this.dataReport.dataTable.docs = this.vhAlgorithm.sortVietnamesebyASC([...this.dataReport.dataTable.docs], value)
    }
    else {
      this.dataReport.dataTable.docs = this.vhAlgorithm.sortVietnamesebyDESC([...this.dataReport.dataTable.docs], value)
    }
  }

  /**
   * cập nhật lại dữ liệu cho chart
   * @param data_chart 
   * @param initStatus 
   */
  updateChart(data_chart, initStatus = false) {
    this.chart.updateOptions(this.initOption(
      !data_chart.length || !data_chart[0].value ? [] : data_chart.map(item => item.value),
      !data_chart.length || !data_chart[0].value ? [] : data_chart.map(item => item.label),
      initStatus
    ))
  }

  goBack() {
    this.router.navigate(['/cafe/dashboard/management/report']);
  }

  /**
   * hiển thị chart ra giao diện
   * @example this.initChart()
   */
  initChart(data_chart?) {
    setTimeout(() => {
      this.chart = new ApexCharts(document.querySelector("#sales-category-chart"), this.initOption([], []));
      this.chart.render();
      if (data_chart) this.updateChart(data_chart)
    }, 200)
  }
  /**
   * bắt sk thay đổi kiểu xem báo cáo (chart or table)
   */
  showChart() {
    if (this.sales_category['_id'] == 'all') this.isChart = !this.isChart;
    else this.isChart = false
    this.isChart ? this.initChart(this.dataReport['dataChart']) : this.chart.destroy()
  }
  gotoDetailProducts(data) {
    let stateObj = { selectedValue: this.selectedValue, id_branch_report: this.id_branch_report, dataReport: this.dataReport }
    this.router.navigate(['/cafe/dashboard/management/report/sales-category/detail-menu'], {
      state: { category: { ...data }, stateObj }
    })
  }
  /**
   * tải về báo cáo dưới dạng hình ảnh của chart
   * @example this.downloadImg()
   */
  downloadImg() {
    this.vhComponent.downloadImg("chart-image",
      `${this.languageService.translate("_category_sales_report")}_${this.languageService.translate("_from")}_${this.format_date(this.dataReport.startTime)}_${this.languageService.translate("_to")}_${this.format_date(this.dataReport.endTime)}_${this.id_branch_report == "all" ? "" : this.vhAlgorithm.changeAlias(this.vhQueryCafe.getlocalBranch(this.id_branch_report).name).replace(/\s/g, "_")}`).then(() => { })
  }
  format_date(date) {
    return this.datePipe.transform(date, "dd_MM_yyyy")
  }

  /**
   * xuất dữ liệu sang file excel và tải về
   */
  export() {
    let data = [];

    let name = `${this.languageService.translate("_category_sales_report")}_${this.languageService.translate("_from")}_${this.format_date(this.dataReport.startTime)}_${this.languageService.translate("_to")}_${this.format_date(this.dataReport.endTime)}_${this.id_branch_report == "all" ? "" : this.vhAlgorithm.changeAlias(this.vhQueryCafe.getlocalBranch(this.id_branch_report).name).replace(/\s/g, "_")}`;
    let total: any = new Object();
    total[this.languageService.translate('No.')] = "";
    total[this.languageService.translate('Category name')] = this.languageService.translate('Total');
    total[this.languageService.translate('Quantity')] = this.dataReport['dataTable'].quantity;
    if (this.reportConfig.sales_btax)
      total[this.languageService.translate('Sales before tax')] = this.dataReport['dataTable'].sales_btax;
    total[this.languageService.translate('Sales after tax')] = this.dataReport['dataTable'].sales_atax;
    data.push(total);

    for (let i in this.dataReport['dataTable'].docs) {
      let item: any = new Object();
      item[this.languageService.translate('No.')] = parseInt(i) + 1;
      item[this.languageService.translate('Category name')] = this.dataReport['dataTable'].docs[i].name;
      item[this.languageService.translate('Quantity')] = this.dataReport['dataTable'].docs[i].quantity;
      if (this.reportConfig.sales_btax)
        item[this.languageService.translate('Sales before tax')] = this.dataReport['dataTable'].docs[i].sales_btax;
      item[this.languageService.translate('Sales after tax')] = this.dataReport['dataTable'].docs[i].sales_atax;
      data.push(item);

      if (parseInt(i) == this.dataReport['dataTable'].docs.length - 1)
        this.vhAlgorithm.exportXLSX(data, name)
    }

    if (!this.dataReport['dataTable'].docs.length)
      this.vhAlgorithm.exportXLSX(data, name)
  }
  ngAfterViewChecked() {
    if (document.querySelector("#sales-category") && document.querySelector(".sales-category-header") && document.querySelector(".ant-table-thead")) {
      this.heightScroll = (document.querySelector("#sales-category").clientHeight -
        document.querySelector(".sales-category-header").clientHeight -
        document.querySelector(".ant-table-thead").clientHeight) - 90 + "px";
    }
    this.cdRef.detectChanges();
  }
}
