import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SalesMenuComponent } from './sales-menu.component';
import { RouterModule, Routes } from '@angular/router';
import { TranslateModule } from '@ngx-translate/core';
import { IonicModule } from '@ionic/angular';
import { NgZorroAntModule } from 'src/app/cafe/desktop/ng-zorro-ant.module';
import { FormsModule } from '@angular/forms';

const routes: Routes = [
  {
    path: '',
    component: SalesMenuComponent
  },
  {
    path: 'detail-menu',
    loadChildren: () => import('./detail-menu/detail-menu.module').then(m => m.DetailMenuModule)
  }
]

@NgModule({
  declarations: [SalesMenuComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    TranslateModule,
    FormsModule
  ]
})
export class SalesMenuModule { }
